import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';

import { getContentCard } from '../actions/contentcard_action';

class ContentCard  extends Component{
	constructor(props){
		super(props);
		
	}

	componentWillMount(){
		this.props.getContentCard();
		
	}


	render(){
		var obj;
		if(this.props.contentdata.viewContentCard.result){
			if(this.props.contentdata.viewContentCard.result.length > 0){
				if (this.props.like){
					
					obj=this.props.contentdata.viewContentCard.result.sort((a,b) => a.likes < b.likes );
					
				}
				if(this.props.trend){
					obj=this.props.contentdata.viewContentCard.result.sort((a,b)=>a.trending < b.trending)
				}
				else {
					obj=this.props.contentdata.viewContentCard.result;
				}
				var my =this;
				var contentMapping = obj.map(function(data){
					
					return (
						<div key={data.id} className={my.props.layout3 == true || my.props.layout4 == false ? "col-xs-6 col-sm-6 col-md-4 col-lg-4 no_padding padding_left" : my.props.layout4 == true || my.props.layout3 == false ? "col-xs-6 col-sm-6 col-md-3 col-lg-3 no_padding padding_left" : "col-xs-6 col-sm-6 col-md-4 col-lg-4 no_padding padding_left"}>
							<div className="panel content-card">
								<img className="img-responsive card-image" src={data.image} alt="content card img"/>
								<div className="panel-body">
									<div className="content-card-section1">
										<div className="content-card-title">
											<p>{data.channel}, <span className="text-muted">{data.timePublished}</span></p>
										</div>
										<div className="content-card-info">
											<a><h3>{data.contentTitle}</h3></a>
											<p className="text-muted">{data.contentDiscription}</p>
										</div>
									</div>
									<div className="content-card-section2">
										<ul>
											<li><img src={data.logo} data-toggle="tooltip" data-placement="top" title="Channel Logo" /></li>
											<li><img src={data.divider} /></li>
											<li><img src={data.quickCategory} data-toggle="tooltip" data-placement="top" title="Quick Category" /></li>
											<li><img src={data.divider} /></li>
											<li><img src={data.contentType} data-toggle="tooltip" data-placement="top" title="Content Type" /></li>
										</ul>
									</div>
									<div className="content-card-footer">
										<ul>
											<li><i className="ico-icon42 footer-icons" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="Trending"></i><span className="text-muted">{data.trending}</span></li>
											<li><img src="../assets/images/content-card/property-divider.png" alt="card-footer items"/></li>
											<li><i className="ico-icon44 footer-icons text-muted" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="Comments"></i><span className="text-muted">{data.comments}</span></li>
											<li><img src="../assets/images/content-card/property-divider.png" alt="card-footer items"/></li>
											<li><i className="ico-icon43 footer-icons text-muted" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="Likes"></i><span className="text-muted">{data.likes}</span></li>
										</ul>
									</div>
								</div>
							</div>
						</div>
					)
						
				})
			
			}
		}
		
		return(
			<div>
				{contentMapping}
			</div>
		)
	}
}

function mapStateToProps(state) {
  const contentdata = state;
  return { contentdata };
}

function mapDispatchToProps(dispatch) {
    return {
    getContentCard: () => {
      dispatch(getContentCard());
    }
  };
}


export default connect(mapStateToProps, mapDispatchToProps)(ContentCard);